import React, {Component} from 'react';
import lodash from 'lodash';

import CardStats from '../components/CardStats';

export default class Dashboard extends Component {
	state = {finances: null }
	componentDidMount() {
		axios.get('/api/dashboard').then(res=>{
			this.setState({finances: res.data});
			window.localStorage['skyschool_dashboard'] = JSON.stringify(res.data);
		}, error => {
			if(!error.response){
				this.setState({finances: JSON.parse(window.localStorage['skyschool_dashboard'])});
			}
		})
	}


	render() {
		let finances = this.state.finances || {};
		return <div className="container-fluid">
			<div className="row">
				<div className="col-lg-4 col-sm-6">
					<CardStats
						category="Students"
						title={finances.students ? finances.students.students : 0}
						icon="nc-cloud-download-93"
						footer={finances.students ? (new Date(finances.students.date)).toDateString() : '-' } />
				</div>
				<div className="col-lg-4 col-sm-6">
					<CardStats
						category="SMS Balance"
						title={finances.smsBalance ? finances.smsBalance : 0}
						icon="nc-bag"
						footer='' />
				</div>
				<div className="col-lg-4 col-sm-6">
					<CardStats 
						category="SMS Sent"
						title={finances.smsSent ? finances.smsSent.sms : 0}
						icon="nc-bank"
						footer={finances.smsSent ? (new Date(finances.smsSent.date)).toDateString() : '-' } />
				</div>
				<div className="col-lg-4 col-sm-6">
					<CardStats
						category="Total Fees"
						title={finances.fees ? finances.fees.total : 0}
						icon="nc-bag"
						footer={finances.fees ? (new Date(finances.fees.date)).toDateString() : '-' } />
				</div>
				<div className="col-lg-4 col-sm-6">
					<CardStats
						category="Total Payment"
						title={finances.payments ? finances.payments.total : 0}
						icon="nc-money-coins"
						footer={finances.payments ? (new Date(finances.payments.date)).toDateString() : '-' } />
				</div>
				<div className="col-lg-4 col-sm-6">
					<CardStats
						category="Total Expenditure"
						title={finances.expenditure ? finances.expenditure.total : 0}
						icon="nc-paper-2"
						footer={finances.expenditure ? (new Date(finances.expenditure.date)).toDateString() : '-'} />
				</div>
			</div>
		</div>
	}
}